.form-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  background-color: #f4f7f6;
}

.image-container {
  flex: 1;
  margin-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

.startup-form {
  flex: 2;
  max-width: 600px;
  margin: 0;
  padding: 20px;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #333;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  font-size: 1rem;
  font-weight: 500;
  color: #7c3c3c;
  display: block;
  margin-bottom: 8px;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #75c8dd;
  border-radius: 5px;
  font-size: 1rem;
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

.input-pair {
  display: flex;
  gap: 15px;
}

.input-pair input {
  flex: 1;
}

.submit-btn {
  background-color: #769640;
  color: rgb(110, 32, 32);
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.submit-btn:hover {
  background-color: #45a049;
}

.back-btn, .next-btn, .save-btn {
  background-color: #769640;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  margin: 10px 5px;
  width: 100px; /* Reduced width */
  transition: background-color 0.3s ease;
}

.back-btn:hover {
  background-color: #d9534f; /* Red hover effect for the Back button */
}

.next-btn:hover, .save-btn:hover {
  background-color: #45a049; /* Green hover effect for Save and Next */
}

.button-group {
  display: flex;
  justify-content: flex-end; /* Align buttons to the right */
  margin-top: 20px;
  gap: 10px; /* Space between the buttons */
}

/* Responsive Design */
@media (max-width: 768px) {
  .form-container {
    flex-direction: column;
    align-items: center;
  }

  .image-container {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .startup-form {
    max-width: none;
    width: 100%;
  }
}
